.fb-fluent-from {
    .components-base-control {
        margin-bottom: 0;
    }
}

.fb-reusable_notice {
    background-color: #fff;
    border-radius: 4px;
    box-shadow: inset 0 0 0 1px #399CFF;
    padding: 1em;
    .title {
        color: #399CFF;
        align-items: center;
        display: flex;
        font-size: 18pt;
        font-weight: 400;
        gap: 15px;
        line-height: 1;
        margin: 0 0 20px;
        text-transform: uppercase;
        svg {
            height: 24px;
            width: auto;
        }
    }
    .content {
        font-size: 13px;
        font-weight: 400;
        margin: 0 0 1em;
        .fb-reusable-notice-text {
            color: #399CFF;
        }
        a {
            color: #399CFF;
            text-decoration: underline;
            font-weight: 600;
        }
    }
}

.fb-fluent-form-select {
    align-items: flex-start;
    box-sizing: border-box;
    color: #399CFF;
    display: flex;
    flex-direction: column;
    font-size: 13px;
    gap: 16px !important;
    margin: 0;
    padding: 24px !important;
    position: relative;
    text-align: left;
    width: 100%;
    -moz-font-smoothing: subpixel-antialiased;
    -webkit-font-smoothing: subpixel-antialiased;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: inset 0 0 0 1px #399CFF;
    outline: 1px solid #399CFF;
    label {
        color: #399CFF;
    }

}